nav a {
  color: #FFFFFF;
}

ul {
  display: flex;
  margin: 20px 0;
  padding: 0;
  list-style: none;
}

li:not(:first-child) {
  margin-left: 30px;
}

a {
  text-decoration: none;
}

a.active {
  color: #fd5e60;
  text-decoration: underline;
}
.qr-reader-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.qr-reader, p {
  width: 320px;
}

p {
  text-align: center;
}

button {
  color: #FFFFFF;
  font-size: 17px;
  border: 1px solid #FFFFFF;
  background: transparent;
  box-shadow: none;
  margin: 20px 0;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  border-color: #fd5e60;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding-top: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
}
